import React,{useState,useEffect,useContext} from 'react'
import axios from 'axios'
import Context from '../../Context';



const ListNV = (props) => {
    const [State, SetState] = useContext(Context);
    return props.data.map((item,index)=>{

        return <tr key={item?.masukien}>
        <th scope="row">{index}</th>
        <td>{item?.masukien}</td>
        <td>{item?.tensukien}</td>
        <td>{item?.chietkhau}</td>
        <td>{item?.noidung}</td>
        <td>{item?.tungay}</td>
        <td>{item?.denngay}</td>
        
      </tr>
    })
}


    
export default function SK() {
    const [State, SetState] = useContext(Context);
   
    useEffect(()=>{
        (async () => {
          let res = await axios.get("http://localhost:8080/NV/SK/GetAllSK")
          if(res.data.data!==undefined){
            SetState({type:"AllSK",payload:res.data.data})
        }
        })()
        
    },[])

   return (
            <div className="container-fluid mt--10">
            {/* table */}
            <h2> Thng tin s kin</h2>
            <form action method="get">
              <table className="table_nhapkho">
                <thead>
                  <tr>
                  
                    <th scope="col">STT</th>
                    <th scope="col">M S Kin</th>
                    <th scope="col">Tn S Kin</th>
                    <th scope="col">Chit Khu</th>
                    <th scope="col">Ni Dung</th>
                    <th scope="col">T Ngy</th>
                    <th scope="col">n Ngy</th>
                  </tr>
                </thead>
                <tbody>
                  <ListNV data={State.AllSK}/>
                </tbody>
              </table>
            </form>
            
          </div>
        )
    
    // GET NV
}
