import React,{useState,useEffect,useContext} from 'react'
import axios from 'axios'
import Context from '../../Context';

const ListCTNV = (props) => {
    return props.data.map((item,index)=>{
        return <tr key={item.machitiethoadon}>
        <th scope="row">{index}</th>
        <td>{item.machitiethoadon}</td>
        <td>{item.tensp}</td>
        <td>{item.masp}</td>
        <td>{item.soluong}</td>
        <td>{item.dongia}</td>
        </tr>
    })
}

const ListNV = (props) => {
    const [State, SetState] = useContext(Context);
    return props.data.map((item,index)=>{
        const Handler_ChitietOnclick = async (event)=>{
            event.preventDefault();
            let res = await axios.get(`http://localhost:8080/SYS/GetAllCTHD/${item.mahoadon}`)
            if(res.data.data!==undefined){
              SetState({type:"AllCTHD",payload:res.data.data});
              SetState({type:"QLHDSTT",payload:-1})
            }
            ///GetAllCTHD/:mahoadon
        }

        return <tr key={item.mahoadon}>
        <th scope="row">{index}</th>
        <td>{item.mahoadon}</td>
        <td>{item.tenhoadon}</td>
        <td>{item.machinhanh}</td>
        <td>{item.makh}</td>
        <td>{item.masukien}</td>
        <td>{item.ngaytao}</td>
        <td>{item.sdt}</td>
        <td>{item.diachi}</td>
        <td>{item.ghichu}</td>
        <td>{item.trangthai}</td>
        <td>{item.tongtien}</td>
        <td>
          <a href="update_nhanvien.html" style={{border: 'solid 1px black'}} onClick={(event)=>Handler_ChitietOnclick(event)}> Chi tit </a>
          
        </td>
      </tr>
    })
}


    
export default function HD() {
    const [State, SetState] = useContext(Context);
   
    useEffect(()=>{
        (async () => {
          let res = await axios.get("http://localhost:8080/SYS/GetAllHD")
          if(res.data.data!==undefined){
            SetState({type:"AllHD",payload:res.data.data})
            SetState({type:"QLHDSTT",payload:0});
        }
        })()
        
    },[])

    switch(State.QLHDSTT){
        case -1: return (
            <div className="container-fluid mt--10">
            {/* table */}
            <h2> Thng tin chi tit ha n {State.AllCTHD[0]?.mahoadon} </h2>
            <form action method="get">
              <table className="table_nhapkho">
                <thead>
                  <tr>
                    <th scope="col">STT</th>
                    <th scope="col">M chi tit ha n</th>
                    <th scope="col">Tn SP</th>
                    <th scope="col">M SP</th>
                    <th scope="col">S lng</th>
                    <th scope="col">n gi</th>
                  </tr>
                </thead>
                <tbody>
                  <ListCTNV data={State.AllCTHD}/>
                </tbody>
              </table>
            </form>
            
          </div>
        )
        // XEM ALL CHI TIET HOA DON
  
        
        
        default:return (
            <div className="container-fluid mt--10">
            {/* table */}
            <h2> Thng tin ha n</h2>
            <form action method="get">
              <table className="table_nhapkho">
                <thead>
                  <tr>
                    <th scope="col">STT</th>
                    <th scope="col">M ha n</th>
                    <th scope="col">Tn ha n</th>
                    <th scope="col">M chi nhnh</th>
                    <th scope="col">M khch hng</th>
                    <th scope="col">M s kin</th>
                    <th scope="col">Ngy to</th>
                    <th scope="col">SDT</th>
                    <th scope="col">a ch</th>
                    <th scope="col">Ghi ch</th>
                    <th scope="col">Trng thi</th>
                    <th scope="col">Tng tin</th>
                    <th scope="col">Tc v khc</th>
                  </tr>
                </thead>
                <tbody>
                  <ListNV data={State.AllHD}/>
                </tbody>
              </table>
            </form>
            
          </div>
        )
    }
    // GET NV
}
